  width: 1px;
  height: 1px;
  text-align: center;
  position: absolute !important;
  left: 50% !important;
  transform: translateX(-50%);
  clip: unset;
  background-color: white;
  border-radius: 0 0 7px 7px;
  overflow: hidden;
}
